<template>
    <home-container title="Change Password" default-href="/home/account/security" :with-header="step !== 1 ? false : true">
        <div v-if="step === 1">
            <ion-label position='stacked'>New Password</ion-label>
            <ion-item class='border ion-margin-bottom' lines='none'>
                <ion-input v-model='password' placeholder="Enter your new password here"></ion-input>
            </ion-item>
            <ion-label position='stacked'>Confirm Password</ion-label>
            <ion-item class='border ion-margin-bottom' lines='none'>
                <ion-input v-model='confirm_password' placeholder="Confirm password here"></ion-input>
            </ion-item>
            
            <ion-button :disabled="!password || !confirm_password || password !== confirm_password" strong @click.prevent="nextStep"  class='ion-margin-horizontal my-5' expand="block">
                <ion-label>Continue</ion-label>
            </ion-button>
        </div>
        <div v-if="step === 2">
            <ion-item lines="none" class="ion-no-padding">
                <ion-button color='dark' slot="start" fill="clear" @click.prevent="step--">
                <ion-icon slot="icon-only" :icon="arrowBackOutline"></ion-icon>
                </ion-button>
                <ion-text slot="start"><h1 class="">Verification</h1></ion-text>
            </ion-item>
            <div class="row my-3">
                <div class="col-9 mx-auto text-center">
                    <ion-text><p class="font-weight-light">Enter 4-digit verification,<br />sent to your email address</p></ion-text>
                </div>
            </div>
            <div class="d-flex justify-content-center">
                <v-otp-input input-type="password"  @on-complete="handleOnComplete" inputClasses="otp-input" :numInputs="4" separator=" "></v-otp-input>
            </div>
            <div class="row my-4">
                <div class="col-9 mx-auto text-center">
                <ion-text
                    ><p class="">
                    Code Expires in,<br />
                    <strong>{{ countdown }}</strong>
                    </p></ion-text
                >
                </div>
            </div>
            <ion-button strong :disabled="!authorized" @click.prevent="processSend" class="ion-margin-horizontal my-4" expand="block">
                <ion-label>Authorize</ion-label>
            </ion-button>
            <div class="my-4">
                <ion-text>
                    <p class="d-flex flex-column align-items-end justify-content-end">
                        <ion-text>Didn't get the code?</ion-text>
                            <strong @click="countDownTick" class="text-primary">Request another code</strong >
                    </p>
                </ion-text
                >
            </div>
        </div>
        <div v-if="step === 3" style="height: 100vh" class="d-flex flex-column align-items-center justify-content-center" >
            <ion-icon class="icon-container mb-5" style="font-size: 4rem" :icon="checkmarkOutline" ></ion-icon>
            <p class='lead text-center'>Your password has been changed<br>  successfully</p>
            <ion-button strong @click.prevent="router.push('/home/account/security')" class="ion-margin-horizontal my-4 w-100" expand="block">
                <ion-label>Home</ion-label>
            </ion-button>
        </div>
    </home-container>
    
</template>
<script>
import { defineComponent } from "vue";
import {arrowBackOutline,checkmarkOutline} from "ionicons/icons";
import { useRouter } from "vue-router";
import { loadingController } from "@ionic/vue";
import VOtpInput from 'vue3-otp-input';

    export default defineComponent({
        components: {VOtpInput},
        data() {
            return {
                password:null,
                confirm_password:null,
                step: 1,
                countdown: null,
                timer: null,
                disableAuthorization: true,
                otp:null,
            }
        },
        async ionViewDidLeave() {
            this.resetFields();
        },
        computed:{
            authorized(){
                return this.otp && !this.disableAuthorization
            }
        },
        setup() {
            const router = useRouter();
            return {
                router,arrowBackOutline,checkmarkOutline
            };
        },
        methods: {
            resetFields() {
                clearInterval(this.timer);
                this.password = null;
                this.confirm_password=null;
                this.step = 1;
                this.countdown = null;
                this.timer = null;
                this.disableAuthorization = true;
                this.otp = null;
            },
            countDownTick() {
                clearInterval(this.timer);
                this.disableAuthorization = false;
                let countDownDate = new Date().setMinutes(new Date().getMinutes() + 1);
                this.timer = setInterval(() => {
                    var now = new Date().getTime();
                    var distance = countDownDate - now;
                    var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
                    var seconds = Math.floor((distance % (1000 * 60)) / 1000);
                    minutes = minutes < 10 ? "0" + minutes : minutes;
                    seconds = seconds < 10 ? "0" + seconds : seconds;
                    if (distance < 2) {
                    clearInterval(this.timer);
                    this.disableAuthorization = true;
                    return;
                    }
                    this.countdown = minutes + ":" + seconds;
                }, 1000);
            },
            handleOnComplete($event){
                this.otp = $event;
                this.disableAuthorization = false;
                this.processSend();
            },
            async processSend(){
                let loading = await loadingController.create({
                    message: "Please wait...",
                });
                loading.present();
                setTimeout(() => {
                    this.step++;
                    loading.dismiss();
                }, 1000);
            },
            nextStep() {
                this.step++;
                clearInterval(this.timer);
                if (this.step === 2) {
                    this.countDownTick();
                }
            },
        }
});

</script>